<template>

  <tbody>

  <nav>

    <router-link style=" text-decoration: none;" to="/Projets/Sites_Web"><h4>Sites Web</h4></router-link>
    <router-link style=" text-decoration: none;" to="/Projets/WebTV"><h4>WebTV</h4></router-link>
    <router-link style=" text-decoration: none;" to="/Projets/Vidéo"><h4>Vidéo</h4></router-link>
    <router-link style=" text-decoration: none;" to="/Projets/Jeu_vidéo"><h4>Jeu vidéo</h4></router-link>
    <router-link style=" text-decoration: none;" to="/Projets/Dessin"><h4>Dessin</h4></router-link>
    <router-link style=" text-decoration: none;" to="/Projets/Motion_Design"><h4>Motion Design</h4></router-link>
    <router-link style=" text-decoration: none;" to="/Projets/Entreprise"><h4>Entreprise</h4></router-link>

  </nav>

  <div class="info">
    <p id="logo">Logo</p>
    <p id="dl"> Telecharger les fichiers</p>
    <p> Description</p>
  </div>

  <div class="projets" v-for="Motion in Motions" :key="Motion.id">
    <h4>{{Motion.Nom}}</h4>

    <img :src="Motion.Image" alt="">
    <a class="dl" href="#" :download="Motion.Archive"> <img src="@/assets/telecharger.png" alt="Telecherger"></a>
    <div class="description">
      {{Motion.Description}}
    </div>
  </div>



  </tbody>
</template>

<script>
export default {
  name: "Motion Design",
  data () {
    return {
      Motions: []
    }
  },
  methods: {



  },
  created() {
    axios.get("https://administration.portfolio.raptorrouge.fr/wp-json/portfolio/v1/Projets-Motions")
      .then( response =>{
        this.Jeux_Videos = response.data;
        console.log(response.data);
        console.log(this.Motions);
      })

  }

}
</script>

<style scoped>

.projets{
  color: white;
  display: grid;
  grid-template-rows: 5vh 20vh 5vh;
  grid-template-columns: 5vh 10vh repeat(3,1fr) 5vh;
}
.info{
  color: white;
  display: grid;
  grid-template-columns: 5vh 10vh repeat(3,1fr) 5vh;
}

nav{
  display: grid;
  grid-template-columns: repeat(7,1fr);
  width: 100vw;
  list-style-type : none;
  padding: 0;
  text-decoration: none;
}

.projets>h4{
  grid-column: 2;
  grid-row: 2;
  margin: auto;
}

.projets>img{
  grid-column: 3;
  grid-row: 2;
  margin: auto;
  width: 10vw;
}

#logo{
  grid-column: 3;
  margin: auto;
}

.dl{
  grid-column: 4;
  grid-row: 2;
  margin: auto;
}

a>img{
  width: 9vw;
}
.description{
  grid-row: 2;
  grid-column: 5;
  margin: auto;
}

</style>



