import Vue from 'vue'
import Router from 'vue-router'
import Accueil from '@/components/Accueil'
import Projets from "../components/Projets";
import Contact from "../components/Contact";
import Options from "../components/Options";
import Mentions_legales from "../components/Mentions-legales";
import Sites_Web from "../components/projets/Sites_Web";
import WebTV from "../components/projets/WebTV";
import Vidéo from "../components/projets/Vidéo";
import Jeu_vidéo from "../components/projets/Jeu-vidéo";
import Dessin from "../components/projets/Dessin";
import Motion_Design from "../components/projets/Motion_Design";
import Entreprise from "../components/projets/Entreprise";


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Accueil',
      component: Accueil
    },{
      path: '/projets',
      name: 'Projets',
      component: Projets
    },{
      path: '/contact',
      name: 'Contact',
      component: Contact
    },{
      path: '/Options',
      name: 'Options',
      component: Options
    },{
      path: '/Mentions_légales',
      name: 'Mentions-legales',
      component: Mentions_legales
    },{
      path: '/Projets/Sites_Web',
      name: 'Sites_Web',
      component: Sites_Web
    },{
      path: '/Projets/WebTV',
      name: 'WebTV',
      component: WebTV
    },{
      path: '/Projets/Vidéo',
      name: 'Vidéo',
      component: Vidéo
    },{
      path: '/Projets/Jeu_Vidéo',
      name: 'Jeu Vidéo',
      component: Jeu_vidéo
    },{
      path: '/Projets/Dessin',
      name: 'Dessin',
      component: Dessin
    },{
      path: '/Projets/Motion_Design',
      name: 'Motion Design',
      component: Motion_Design
    },{
      path: '/Projets/Entreprise',
      name: 'Entreprise',
      component: Entreprise
    }



  ]
})




