<template>

  <tbody id="presentation">

    <div v-for="presentation in presentations" :key="presentation.id" :class="'photo' + presentation.id">
      <img :src="presentation.Image" alt="">
        <div class="hover">
          <h2> {{presentation.Nom}} </h2>
          <h3> {{presentation.Nom_personne}} </h3>
          <p> {{presentation.Description}} </p>
        </div>
    </div>



  </tbody>

</template>

<script>
import axios from "axios";
import App from "../App";


export default {
  name: 'Accueil',
  data () {
    return {
      presentations: [],
    }
  },
  methods: {

    Getpresentation: function (){

      axios.get("https://administration.portfolio.raptorrouge.fr/wp-json/portfolio/v1/Presentation")
      .then( response =>{
        this.presentations = response.data;
        console.log(response.data);
        console.log(this.presentations);
      })
    }
  },
  mounted() {
    this.Getpresentation();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

body{
  margin: 0;
}

#presentation{
  height: 90vh;
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-template-rows: 90vh ;
  text-align: justify;


}

.hover{
  display: none;
  color: white;
  align-content: center;
  margin: auto;
}

.photo1:hover>.hover{
  position: relative;
  display: block;
  z-index: 1;
  margin-top: -50vh;
  width: 50%;

}

.photo0:hover>.hover{
  position: relative;
  display: block;
  z-index: 1;
  margin-top: -55vh;
  width: 50%;

}
.photo0:hover>img{
  filter: brightness(65%);

}
.photo1:hover>img{
  filter: brightness(65%);

}

.photo0{
  grid-row: 1;
  grid-column: 1;
  display: grid;
  grid-template-rows: repeat(3,1fr);
  grid-template-columns: 1fr;
  height: 89vh;
}

.photo0>img{
  width: 100%;
  height: 90vh;

}

.photo1{
  grid-row: 1;
  grid-column: 2;
  display: grid;
  grid-template-rows: repeat(3,1fr);
  grid-template-columns: 1fr;
  height: 89vh;
}
.photo1>img{
  width: 100%;
  height: 90vh;
  z-index: 0;
}

</style>



