<template>
  <tbody>
  <nav>

    <router-link style=" text-decoration: none;" to="/Projets/Sites_Web"><h4>Sites Web</h4></router-link>
    <router-link style=" text-decoration: none;" to="/Projets/WebTV"><h4>WebTV</h4></router-link>
    <router-link style=" text-decoration: none;" to="/Projets/Vidéo"><h4>Vidéo</h4></router-link>
    <router-link style=" text-decoration: none;" to="/Projets/Jeu_vidéo"><h4>Jeu vidéo</h4></router-link>
    <router-link style=" text-decoration: none;" to="/Projets/Dessin"><h4>Dessin</h4></router-link>
    <router-link style=" text-decoration: none;" to="/Projets/Motion_Design"><h4>Motion Design</h4></router-link>
    <router-link style=" text-decoration: none;" to="/Projets/Entreprise"><h4>Entreprise</h4></router-link>

  </nav>

  </tbody>
</template>

<script>

import axios from "axios";
import App from "../App";

export default {
  name: "'Projets'",
  data () {
    return {
      projets: [],
      Categories: [{Nom: 'Site_Web', View: 'false'},
                   {Nom: 'WebTV' , View: 'false'},
                   {Nom: 'Vidéo' , View: 'false'},
                   {Nom: 'Jeu_Vidéo', View: 'false'},
                   {Nom: 'Dessin', View: 'false'},
                   {Nom: 'Motion_Design', View: 'false'},
                   {Nom: 'Entreprise', View: 'false'}]
    }
  },
  methods: {

    GetProjets: function (){

      axios.get("https://administration.portfolio.raptorrouge.fr/wp-json/portfolio/v1/Projets")
        .then( response =>{
          this.projets = response.data;
          console.log(response.data);
          console.log(this.projets);
        })
    },

    click: function ( div ){

    }
  },
  mounted() {
    this.GetProjets();
  }
}
</script>

<style scoped>

nav{
  display: grid;
  grid-template-columns: repeat(7,1fr);
  width: 100vw;
  list-style-type : none;
  padding: 0;
  text-decoration: none;
}
h4{
  color: white;
  text-decoration: none;
  box-shadow: none;

}
router-link{
  text-decoration: none;
}



</style>



